const Label = styled.label({
  color: theme.colors.grey_400,
})

const copyToClipboard = str => {
  const el = document.createElement('textarea')
  el.value = str
  document.body.appendChild(el)
  el.select()
  document.execCommand('copy')
  document.body.removeChild(el)
}

const formatCode = (value, type) => {
  const code = `(function() {
    var data = ${value};
    var script = document.createElement('script');
    script.type = 'application/ld+json';
    script.innerHTML = JSON.stringify(data);
    document.head.appendChild(script);
  })();`

  switch (type) {
    default:
    case 'prettified':
      const formattedCode = prettier.format(code, {
        parser: 'babel',
        plugins: [parserBabylon],
      })
      return `<script>\n${formattedCode}</script>`
    case 'minified':
      const { code: minifiedCode } = UglifyJS.minify(code)
      return `<script>${minifiedCode}</script>`
  }
}

const stripScriptTags = value => {
  return value.replace(/<\/?script[^>]*(>|$)/gim, '')
}

const Form = () => {
  const [input, setInput] = useState('')
  const [output, setOutput] = useState('')
  const [outputType, setOutputType] = useState('minified')
  const [error, setError] = useState(false)
  const formattedOutput = output && !error ? formatCode(output, outputType) : ''

  return (
    <div
      css={{
        display: 'grid',
        gridTemplateColumns: '1fr',
        gridGap: rem(20),
        padding: rem(20),
        [media.tabletLg]: {
          gridTemplateColumns: '1fr 1fr',
          gridGap: rem(50),
          padding: rem(50),
        },
        background: '#fff',
        borderRadius: 8,
        boxShadow: '0 5px 30px #CBD6D9',
      }}
    >
      <div>
        <Label htmlFor="input-code">JSON-LD input</Label>
        <Space y={10} />
        <TextArea
          id="input-code"
          autosize={{ minRows: 8 }}
          value={input}
          onChange={e => setInput(e.target.value)}
          css={{ '&.ant-input': { height: 178 } }}
        />
        <Space y={25} />
        {error && (
          <Fragment>
            <Alert
              message={
                <form
                  method="post"
                  target="_blank"
                  action="https://search.google.com/structured-data/testing-tool"
                >
                  <p css={{ display: 'inline' }}>Please enter valid code.</p>
                  <Space inline x={10} />
                  <Button type="ghost" size="small" htmlType="submit">
                    Test on Google
                  </Button>
                  <textarea
                    name="code"
                    value={input}
                    css={{ display: 'none' }}
                  />
                </form>
              }
              type="error"
            />
            <Space y={25} />
          </Fragment>
        )}
        <Button
          type="primary"
          size="large"
          onClick={() => {
            setError(false)

            if (!input) setError(true)

            const jsonInput = stripScriptTags(input)

            try {
              JSON.parse(jsonInput)
            } catch {
              setError(true)
              return
            }

            setOutput(jsonInput)
          }}
        >
          Generate Tag Manager Code
        </Button>
      </div>
      <div>
        <Label htmlFor="output-code">Google Tag Manager Friendly Code</Label>
        <Space y={10} />
        <TextArea
          id="output-code"
          autosize={{ minRows: 8 }}
          value={formattedOutput}
          disabled={!output}
          css={{ '&.ant-input': { height: 178 } }}
        />
        <Space y={25} />
        <div
          css={{
            display: 'flex',
            flexWrap: 'wrap',
            justifyContent: 'space-between',
            alignItems: 'center',
            marginTop: -20,
            marginLeft: -20,
            '> *': {
              marginTop: 20,
              marginLeft: 20,
            },
          }}
        >
          <Button
            type="primary"
            size="large"
            disabled={!output}
            onClick={() => {
              copyToClipboard(formattedOutput)
              message.success('Copied to clipboard!')
            }}
          >
            Copy Tag Manager Code
          </Button>
          <div css={{ color: theme.colors.grey_200 }}>
            <Radio.Group
              value={outputType}
              onChange={e => setOutputType(e.target.value)}
              size="large"
              disabled={!output}
            >
              <Radio value="minified">Minified</Radio>
              <Radio value="prettified">Prettified</Radio>
            </Radio.Group>
            <a
              href="#should-i-choose-prettified-or-minified-version"
              title="Should I choose Prettified OR Minified version?"
            >
              (What is this?)
            </a>
          </div>
        </div>
      </div>
    </div>
  )
}

